/**
 * Helpers / Animations / Contact Button
 * Contact button animation
 */

@keyframes contact-button {

    0% {
        top: 0;
        filter: blur(0px);
    }

    30% {
        top: -2px;
        filter: blur(5px);
    }

    50% {
        top: -1px;
        filter: blur(3px);
    }

    100% {
        top: 0;
        filter: blur(0px);
    }

}
